import app from 'firebase/app'
import 'firebase/auth'
import 'firebase/firebase-firestore'

const config = {
	apiKey: "AIzaSyCCOP1BvurjazlSRaglbMf0yNwTbIeozWE",
	authDomain: "fir-react-auth-99b0d.firebaseapp.com",
	projectId: "fir-react-auth-99b0d",
	storageBucket: "fir-react-auth-99b0d.appspot.com",
	messagingSenderId: "10751172366",
	appId: "1:10751172366:web:ecfa242fc18b608fd0015f"
}

class Firebase {
	constructor() {
		app.initializeApp(config)
		this.auth = app.auth()
		this.db = app.firestore()
	}

	login(email, password) {
		return this.auth.signInWithEmailAndPassword(email, password)
	}

	logout() {
		return this.auth.signOut()
	}

	async register(name, email, password) {
		await this.auth.createUserWithEmailAndPassword(email, password)
		return this.auth.currentUser.updateProfile({
			displayName: name
		})
	}

	isInitialized() {
		return new Promise(resolve => {
			this.auth.onAuthStateChanged(resolve)
		})
	}

	getCurrentUsername() {
		return this.auth.currentUser && this.auth.currentUser.displayName
	}

}

export default new Firebase()